.current-palylist {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}
.current-palylist .mask {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.75);
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.current-palylist .card {
  width: 100%;
  min-height: 180px;
  background: white;
  max-height: 60vh;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}
