.control {
  width: 100%;
}
.control .progerss {
  margin-left: 10%;
  width: 80%;
  height: 10px;
  position: relative;
  display: flex;
  align-items: center;
}
.control .progerss input {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 1;
  opacity: 0;
}
.control .progerss .line {
  background: white;
  width: 100%;
  height: 2px;
  border-radius: 10px;
}
.control .progerss .dot {
  width: 10px;
  height: 10px;
  background: white;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  margin-left: -5px;
  z-index: 0;
  box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.3);
}
.control .icons {
  align-items: center;
  box-sizing: border-box;
  margin: 45px 0;
  display: flex;
  justify-content: space-around;
}
.control .icons li img {
  width: 32px;
  height: 32px;
}
.control .icons li.icon-open {
  border: 2px solid #ccc;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  position: relative;
}
.control .icons li.icon-open span {
  width: 20px;
  height: 20px;
}
.control .icons li.icon-open span.paused-icons::after {
  width: 29px;
  height: 30px;
  display: block;
  content: '';
  border-left: 6px solid #ccc;
  border-right: 6px solid #ccc;
  transform: translate(30%, 30%);
}
.control .icons li.icon-open span.play-icons {
  overflow: hidden;
}
.control .icons li.icon-open span.play-icons::before {
  content: "";
  width: 29px;
  height: 29px;
  display: block;
  border-top: 15px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 15px solid transparent;
  border-left: 19px solid #ccc;
  position: absolute;
  top: 50%;
  left: 88%;
  transform: translate(-85%, -50%);
}
