




































li.musiclist-item {
  display: flex;
  // background: lightblue;
  // margin: 10px  10px;

  align-items: center;

  .index {
    margin-left: 10px;
    // font-size: 1.2em;
  }
  &:nth-child(1),
  &:nth-child(2),
  &:nth-child(3) {
    .index {
      color: red;
    }
  }

  .info {
    flex: 1;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #f5f5f5;
    padding: 10px 0;
    padding-right: 10px;
    margin-left: 10px;
    .detail {
      line-height: 1.3em;
      flex: 1;
      h3 {
        
      }
      p {
        color: #c4c4c4;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
        font-size: 12px;
        .artist {
          &::after {
            content: "/";
          }
        }
        span {
          &:last-of-type {
            // background: red;
            &::after {
              content: "";
            }
          }
        }
        i {
          font-style: normal;
          // white-space: nowrap;
        }
      }
    }
  }

  .icon {
    width: 22px;
    height: 22px;
    background-image: url("../assets/index_icon_2x.png");
    background-size: 166px 97px;
    background-position: -24px 0;
  }
}
