@keyframes disc-rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.disc {
  margin: 0 auto;
  width: 247px;
  height: 247px;
  position: relative;
}
.disc::after {
  content: "";
  position: absolute;
  top: -50px;
  left: 112px;
  width: 73px;
  height: 118px;
  -webkit-transform: rotate(-7deg);
  -ms-transform: rotate(-7deg);
  transform: rotate(-7deg);
  -webkit-transform-origin: left top;
  -ms-transform-origin: left top;
  transform-origin: left top;
  background: url(http://s3.music.126.net/mobile-new/img/needle-ab.png?4b81da043356110081e5ccaed2e6f2ad=) no-repeat;
  background-size: contain;
  z-index: 5;
}
.disc .m-song-disc {
  width: 100%;
  height: 100%;
}
.disc .m-song-disc::before {
  background-image: url(https://p5.music.126.net/obj/wo3DlcOGw6DClTvDisK1/4602256867/5855/63fb/325b/d7e4e3a244701ee85fecb5d4f6b5bd57.png?imageView=&quality=75&thumbnail=200y200);
  content: " ";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 2;
  background: url(http://s3.music.126.net/mobile-new/img/disc.png?d3bdd1080a72129346aa0b4b4964b75f=) no-repeat;
  background-size: contain;
}
.disc .m-song-disc .m-song-turn {
  width: 184px;
  height: 184px;
  margin: -92px 0 0 -92px;
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 1;
}
.disc .m-song-disc .m-song-turn .m-song-rollwrap {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  overflow: hidden;
  background: url(http://s3.music.126.net/mobile-new/img/disc_default.png?ba7c53e25dbac9fe14f024846ddb57d2=) no-repeat;
  background-size: contain;
}
.disc .m-song-disc .m-song-turn .m-song-rollwrap .u-img {
  width: 100%;
  vertical-align: middle;
  border-radius: 50%;
}
.paused .u-img {
  animation: disc-rotate 10s linear infinite;
}
