.head-card {
  padding: 30px 10px 30px 15px;
  position: relative;
  overflow: hidden;
}
.head-card .hd-aft::after {
  content: " ";
  background-color: rgba(0, 0, 0, 0.25);
}
.head-card .hd-aft .hd-img1 {
  width: 100%;
  height: 100%;
  -webkit-filter: blur(20px);
  filter: blur(20px) brightness(0.5);
  position: absolute;
  top: -10%;
  left: 0;
  transform: scale(1.5);
  z-index: -1;
}
.head-card .hd-aft .pl-fl {
  display: flex;
}
.head-card .hd-aft .pl-fl .pl-fl1 {
  color: white;
  width: 30%;
  position: relative;
  /* img{
       }*/
}
.head-card .hd-aft .pl-fl .pl-fl1 span {
  font-size: 14px;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  width: 34px;
  line-height: 1em;
  display: inner-block;
  background-color: #16BD74;
  position: absolute;
  top: 7%;
  left: 0;
}
.head-card .hd-aft .pl-fl .pl-fl1 i {
  margin: 2px 2px 0 0;
  font-size: 12px ;
  font-style: normal;
  position: absolute;
  top: 0;
  right: 0;
}
.head-card .hd-aft .pl-fl .pl-fl1 i::before {
  content: "";
  width: 1em;
  height: 1em;
  display: inline-block;
  background: url("data:image/svg+xml;base64,");
  background-repeat: no-repeat;
  vertical-align: bottom;
  margin-right: 3px;
}
.head-card .hd-aft .pl-fl .hd-fr {
  margin-left: 16px;
  width: 60%;
  flex: 1;
}
.head-card .hd-aft .pl-fl .hd-fr h2 {
  padding-top: 1px;
  font-size: 17px;
  line-height: 1.3;
  color: #fefefe;
  height: 44px;
  display: -webkit-box;
  -webkit-box-pack: center;
}
.head-card .hd-aft .pl-fl .hd-fr .hd-au {
  margin-top: 20px;
  color: hsla(0, 0%, 100%, 0.7);
}
.head-card .hd-aft .pl-fl .hd-fr .hd-au img {
  vertical-align: middle;
  display: inner-block;
  width: 30px;
  border-radius: 50%;
}
.head-card .hd-aft .pl-fl .hd-fr .hd-au i {
  font-size: 12px;
  font-weight: 500;
  font-style: normal;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
