
































.search{
    .inp{
        width: 100%;
        height: 50px;
        border-bottom: 1px solid #f1f1f1;
        input{
        border: none;
        outline: medium;
        background-color: #f1f1f1;
        border-radius: 16px;
        margin: 15px auto;
        display: block;
        width: 90%;
        height: 34px;
        padding: 0 20px;
        }
        }
    ul{
       
        margin: 0 auto;
        li{
            margin: 15px 0 5px 30px;
            border-bottom: 1px solid #f1f1f1;
            padding-bottom: 10px;
        }
    }
}
