li.current-play-list-item {
  position: relative;
  display: flex;
  margin: 10px;
}
@keyframes shensuo {
  from {
    height: 10%;
  }
  to {
    height: 100%;
  }
}
li.current-play-list-item .palying-animate {
  width: 14px;
  height: 14px;
  background: white;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin: 0 5px;
}
li.current-play-list-item .palying-animate span {
  width: 20%;
  height: 20%;
  background: red;
  animation: shensuo 0.6s linear infinite alternate;
}
li.current-play-list-item .palying-animate span:nth-child(1) {
  animation-delay: 0s;
}
li.current-play-list-item .palying-animate span:nth-child(2) {
  animation-delay: -0.2s;
}
li.current-play-list-item .palying-animate span:nth-child(3) {
  animation-delay: -0.4s;
}
li.current-play-list-item .palying-animate.paused span {
  animation-play-state: paused;
}
li.current-play-list-item .dtail-a {
  display: flex;
}
li.current-play-list-item .dtail-a .detail {
  flex: 1;
}
li.current-play-list-item .dtail-a .detail .artist {
  font-size: 12px;
  color: #c2c2c2;
}
li.current-play-list-item .dtail-a .delete {
  position: absolute;
  right: 4px;
  top: 2px;
}
