.discuss {
  margin: 10px;
  display: flex;
}
.discuss img {
  display: inline-block;
  margin-right: 10px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
.discuss .discuss-fl {
  width: 100%;
  border-bottom: 1px solid #f5f5f5;
}
.discuss .discuss-fl .discuss-cont {
  width: 100%;
  margin-bottom: 20px;
}
.discuss .discuss-fl .discuss-cont span {
  font-size: 12px;
}
.discuss .discuss-fl .discuss-cont span.cont {
  color: #a0a0a0;
  float: right;
}
.discuss .discuss-fl span {
  font-size: 14px;
}
